@import "../../../utils";

.Main {
  $deep-blue: #282c34 !default;
  $blue: #61dafb !default;
  word-break: keep-all;
  padding: 30px 0 0 0;
  text-align: center;
  font-family: "Nanum Brush Script", cursive;

  header {
    background-color: $deep-blue;
    //background-image: url("../../../img/noboo.jpeg");
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    //min-height: 110vh;
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;

    @include media("<medium") {
      min-height: 50vh;
    }

    .Main-Container {
      @include media("<medium") {
        .Main-Text {
          font-size: 1.8rem;
        }
        .Main-subText {
        }
      }
    }
  }
  .Main-title {
    color: $blue;
  }
  .Main-subText {
    font-weight: normal;
  }
  .Main-button {
    border: 1px solid $blue;
    background-color: $deep-blue;
    border-radius: 5px;
    color: $blue;
    font-size: 1rem;
    z-index: 0;
    &:hover {
      background-color: $blue;
      border: 1px solid $blue;
      color: $deep-blue;
    }
  }
  @include media("<medium") {
    .Main-button {
      margin-top: 1rem;
      border: 1px solid $blue;
      background-color: $deep-blue;
      border-radius: 5px;
      color: $blue;
      //font-size: 0.8rem;
      //width: 5rem;
      z-index: 0;

      &:hover {
        background-color: $blue;
        border: 1px solid $blue;
        color: $deep-blue;
      }
    }
  }
  .Main-border {
    position: absolute;
    bottom: 0px;
    height: 50px;
    width: 0px;
    border: 1px solid white;
    border-right: 0px;
  }
  span {
    /*color: #282c34;*/
  }
}
